import React from 'react';
import Typography from '@mui/material/Typography';
import PageContainer from '../components/PageContainer';

function About() {
  return (
    <PageContainer maxWidth="sm">
      <Typography 
        variant="h4" 
        component="h1" 
        gutterBottom
        sx={{ 
          fontFamily: "'Noto Serif SC', serif",
          fontWeight: 500,
          color: '#2c5530'
        }}
      >
        
      </Typography>
      <Typography 
        variant="body1"
        sx={{ 
          fontFamily: "'Noto Serif SC', serif",
          color: '#333'
        }}
      >
        ""
        
      </Typography>
    </PageContainer>
  );
}

export default About;
